.comp-form-copy {

    .slides-wrap {
        @include full-width;
        margin-top: 30px;
        margin-bottom: 30px;
    }

    @include media-breakpoint-up(md) {
        .slides {
            height: 500px;
        }

        .short .slides {
            height: 300px;
        }

        .tall .slides {
            height: 700px;
        }
    }



    .slides .carousel-item {
        height: 100%;
    }

    .slides .slide {
        height: 100%;
        display: flex;
        justify-content: space-between;

        background: $light-grey;
    }

    .slide-image {
        width: calc(100%/12 * 8);


        padding: 15px;
        .debug & {
            border: 1px dotted $debug-blue;
        }
    }

    .slide-info {
        width: calc(100%/12 * 4);


        padding: 15px;
        .debug & {
            border: 1px dotted $debug-blue;
        }
    }





    @include media-breakpoint-down(sm) {
        .slides .slide {
            flex-direction: column;
        }

        .slide-image {
            width: 100%;
            height: 400px;
        }

        .slide-info {
            min-height: 250px;
            width: 100%;
        }

        .slide-image img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }




    .slide-image img {
        display: block;
        margin: 0;

        max-width: 100%;
        max-height: 100%;
    }

    .cover .slide-image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }



    .slide-info .artist {
        font-size: .8em;
    }

    .slide-info .title {
        font-family: 'Miriam Libre';
        font-size: 24px;
        letter-spacing: 0.31px;
        line-height: 25px;
        font-weight: 300;
        color: $dark-grey;
    }

    .slide-info .comments {
        margin-top: 30px;
        margin-bottom: 30px;
    }


    .slide-info-bottom a {
        display: block;
        width: max-content;
        margin-bottom: 10px;
    }

    .slide-info-top {

        height: 40px;
        position: absolute;
        width: 100%;
        bottom: 0;
    }

    .carousel-control-prev {
        left: calc(100%/12*8);
    }

    .carousel-control-prev,
    .carousel-control-next,
    .carousel-control-prev:hover,
    .carousel-control-next:hover {
        background: none;
        border-bottom: none;
        width: 50px;
    }
}

.out {}



